<template>
  <div class="list">
      <div class="city-item">
          <p>
              定位/附近城市
          </p>
           <div v-for="item2 in mapCities" :key="item2.id"  @click="Tohome(item2.name)">
             {{item2.name}}
           </div>
      </div>
      <div class="city-item">
          <p>
              已开通周边游站点
          </p>
           <div v-for="item in cities" :key="item.id" @click="Tohome(item.name)">
               {{item.name}}
           </div>
      </div>
      <div class="city-item">
          <p>
              热门玩乐城市
          </p>
           <div v-for="item1 in hotCities" @click="Tohome(item1.name)" :key="item1.id">
               {{item1.name}}
           </div>
           
      </div>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
export default {
   
    data(){
        return{
            mapCities:[],
            cities:[],
            hotCities:[],
      
    
     
        }
    },
    methods:{
        Tohome(city){
           this.changeCity(city);
            this.$router.push({
                path:'/'
            })
        },
        ...mapMutations(['changeCity'])
    },
  //city城市的数据渲染
mounted () {
   let That=this
    this.axios.get("./ipa/city(1).json")
    .then((res)=>{
        console.log(res)
        let data=res.data.data;
          That.mapCities=data.mapCities;
          That.cities=data.cities;
          That.hotCities=data.hotCities
    })
}
}
</script>

<style scoped>
.city-item{
    padding-left: .4rem;
    color: #999;
    font-size: .4rem;
}
.city-item p{
   padding: .3rem 0;
}
.city-item div{
    display: inline-block;
    width: 2rem;
    height: .82rem;
    line-height: .82rem;
    border: 1px solid #ccc;
    border-radius: .2rem;
    text-align: center;
    margin-right: .4rem;
    box-sizing: border-box;
    margin-bottom: .2rem;

}
.list{
    cursor: pointer;
}
</style>